.project {
  position: relative;
  left: 0;
  display: inline-block;
  max-width: 100%;
  margin-bottom: 40px;
  padding: 0;
  border-radius: 8px;
  transition: all 0.5s; }
  .project__image {
    display: block;
    max-width: 100%;
    border-radius: 8px;
    overflow: hidden; }
    .project__image img {
      display: block;
      max-width: 100%; }
  .project__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    opacity: 0;
    transition: all 0.5s;
    border-radius: 8px;
    box-shadow: 0 12px 42px rgba(0, 0, 0, 0.24); }
    .project__overlay--campusonline {
      background-color: rgba(249, 0, 70, 0.54); }
    .project__overlay--krenn-reisen {
      background-color: rgba(0, 0, 69, 0.24); }
  .project__name {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0%) scale(0.96);
    color: #ffffff;
    font-size: 3.2rem;
    font-weight: 800;
    letter-spacing: -0.01em;
    text-shadow: 0 4px 24px rgba(0, 0, 0, 0.28);
    transition: all 0.8s; }
  .project:hover {
    transform: translateY(-8px); }
    .project:hover .project__overlay {
      opacity: 1; }
    .project:hover .project__name {
      transform: translate(-50%, -50%) scale(1); }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: opacity 0.2s ease-in-out; }

*::selection {
  background: rgba(0, 0, 0, 0.1); }

html {
  font-size: 62.5%; }

body {
  font-family: 'San Francisco', -apple-system, BlinkMacSystemFont, '.SFNSText-Regular', Roboto, 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
  background-color: #fcfcfc;
  color: #343134; }

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  box-shadow: inset 0 -0.3em 0 0 rgba(60, 210, 160, 0.26), 0 0.1em 0 0 rgba(41, 178, 132, 0.26);
  transition: box-shadow 0.14s,-webkit-box-shadow 0.14s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  a:hover {
    box-shadow: inset 0 -1.2em 0 0 rgba(60, 210, 160, 0.26), 0 0.1em 0 0 rgba(41, 178, 132, 0.26); }

.container {
  position: relative;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  box-sizing: border-box; }
  @media (max-width: 1600px) and (min-width: 1200px) {
    .container {
      max-width: 1000px; } }
  @media (max-width: 1200px) {
    .container {
      max-width: 80%; } }
  @media (max-width: 500px) {
    .container {
      margin: 0 auto 20px; } }
  .container--full-screen {
    max-width: 100%;
    min-height: 100vh; }

.header {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 25px 75px 0 75px; }
  @media (max-width: 1024px) {
    .header {
      display: block;
      text-align: center; } }
  .header__logo, .header__navigation {
    flex: 1 1 auto; }
  .header__logo {
    display: inline-block;
    flex: 1 1 auto; }
  .header__navigation {
    text-align: right;
    line-height: 48px; }
    @media (max-width: 1024px) {
      .header__navigation {
        display: block;
        margin-top: 12px;
        text-align: center;
        line-height: 1; } }

.logo__link {
  display: inline-block;
  box-shadow: none; }
  .logo__link .curly-brackets,
  .logo__link .colon,
  .logo__link .letter {
    fill: #ebebec;
    transition: fill 0.12s ease-in-out; }
  .logo__link:hover {
    box-shadow: none; }
    .logo__link:hover .curly-brackets {
      fill: #343134; }
    .logo__link:hover .colon,
    .logo__link:hover .letter {
      fill: #3cd2a0; }

.navigation__element {
  color: rgba(52, 49, 52, 0.54);
  font-size: 1.3rem;
  font-weight: 400;
  text-transform: uppercase; }
  .navigation__element--selected {
    color: #343134; }
  .navigation__element:not(:first-child) {
    margin-left: 35px; }
  .navigation__element, .navigation__element:hover {
    box-shadow: none; }

.hero {
  padding: 120px 0; }
  .hero__title {
    max-width: 360px;
    font-size: 45px;
    font-weight: 300;
    line-height: 53px; }
  .hero__introduction {
    max-width: 360px;
    font-size: 1.8rem;
    line-height: 26px; }
    .hero__introduction:not(:last-child) {
      margin-bottom: 2.5rem; }

.highlight {
  font-style: normal;
  color: #3cd2a0; }

.footer {
  padding: 60px 0 40px;
  color: rgba(52, 49, 52, 0.54);
  font-size: 1.3rem;
  text-transform: uppercase; }
  .footer .container {
    display: flex;
    flex-direction: row; }
    @media (max-width: 1024px) {
      .footer .container {
        display: block;
        text-align: center; } }
  .footer__love, .footer__copyright {
    flex: 1 1 auto; }
  .footer__copyright {
    text-align: right; }
    @media (max-width: 1024px) {
      .footer__copyright {
        text-align: center; } }

.content--projects-list a, .content--projects-list a:hover {
  box-shadow: none; }

.content--text {
  max-width: 600px; }
  .content--text p {
    margin-bottom: 1.2rem;
    font-size: 1.4rem;
    line-height: 2rem; }
